.contact-us {
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  &__info {
    margin: 0 0 24px;

  }

  &__info-title {
    font-size: 14px;
    line-height: 140%;
    color: $colorWhite;
    margin: 0 0 8px;
  }

  &__info-link {
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: $colorWhite;
    text-decoration: none;
    transition: all $transition-time;

    &:hover {
      color: #e5e5e5;
      text-decoration: underline;
    }
  }

  &__filed {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
