.header {
  background-image: url(../images/Cover_photo.png);
  background-position: center;
  background-size: cover;

  @include on-phone {
    background-image: url(../images/Cover_photo_mobile.png);
    background-position: right;
  }


  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    box-sizing: border-box;

    @include on-phone {
      padding: 29px 0 88px;
    }

    @include on-tablet {
      padding: 36px 0 66px;
    }

    @include on-desktop {
      padding: 36px 0 123px;
    }
  }

  &__icons-container {
    display: flex;
    gap: 30px;

    @include on-tablet {
      gap: 40px;
    }

    @include on-desktop {
      gap: 40px;
    }
  }

  &__call {
    text-decoration: none;

    @include on-desktop {
      &::before {
        content: "+1 234 5555-55-55";
        position: relative;
        top: 50%;
        transform: translate(-100%, -50%);

        display: block;
        width: max-content;
        padding-right: 20px;

        font-weight: 700;
        font-size: 12px;
        line-height: 16px;
        color: #fff;
        letter-spacing: 2px;
        text-transform: uppercase;
        opacity: 0;
        pointer-events: none;
      }

      &:hover::before {
        opacity: 1;
        pointer-events: all;
        transition: $transition-time;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    color: #fff;
    letter-spacing: -1.5px;
    text-align: center;

    transition: all $transition-time;

    &:hover {
      text-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
    }

    @include on-desktop {
      font-weight: 700;
      font-size: 80px;
      line-height: 100%;
    }

    @include on-tablet {
      font-weight: 700;
      font-size: 64px;
      line-height: 100%;
    }

    @include on-phone {
      font-weight: 700;
      font-size: 36px;
      line-height: 100%;
    }
  }
}
